}

.App {
  text-align: center;
  background-color: #0A192F;
  font-family: 'Roboto Mono', monospace;
  width: 100vw;
}

/* width */
::-webkit-scrollbar {
  width: 5px;
  height: 5px;
  border-radius: 40px;
}

/* Track */
::-webkit-scrollbar-track {
  box-shadow: inset 0 0 5px rgb(136, 129, 129); 
  background-color: #0A192F;
  border-radius: 40px;
  width: 5px;
}
 
/* Handle */
::-webkit-scrollbar-thumb {
  background: #8892B0; 
  border-radius: 10px;
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
  background: #8892B0; 
}

