.navbar {
    display: flex;
    /* float:right; */
    /* padding-left: 7vh; */
    background-color: #0A192F;
    width: 100vw;
    height: 80px;
}

.left {
    flex: 50% 1;
    display: flex;
    justify-content: left;
    align-items: center;
    /* background-color: green; */
}

.right {
    flex: 50% 1;
    /* background-color: red; */
    /* margin-left: 20px; */
    display: flex;
    justify-content: right;
    align-items: center;
}

.right .links {
    max-height: 80px;
}

.right .links > a {
    text-decoration: none;
    color: #63F4DA;
    font-size: 16px;
    margin-left: 20px;
    margin-right: 15px;
}

.right .links > a:hover {
    color: #8892B0;
}

.right > .MuiSvgIcon-root {
    display: none;
    /* float:right; */
    margin-left: 70%;
}

.right #hidden {
    display: flex;
}

.left .me {
    max-height: 80px;
}

.left .me > h1 {
    font-family: 'Poppins', 'sans-serif';
    color: #63F4DA;
    margin-left: 40px;
}

@media only screen and (max-width: 850px) {
    .right .links > a {
        font-size: 20px;
    }
}

@media only screen and (max-width: 750px) {
    .right .links > a {
        font-size: 15px;
    }
}

@media only screen and (max-width: 650px) {
    .right .links {
        display: none;
    }
    .right > .MuiSvgIcon-root {
        display: flex;
    }
    .right #hidden {
        position: absolute;
        right: 0px;
        top: 80px;
        height: 230px;
        max-height: 230px;
        width: 100%;
        background-color:#0A192F; 
        display: flex;
        flex-direction: column;
        align-items: center;
    }

    .right #hidden > a {
        margin: 5px;
    }
}


