.card {
    overflow: hidden;
    box-shadow: 0 2px 20px rgb(0, 0, 0, 0.75);
    border-radius: 10px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    cursor: pointer;
    transition: -webkit-transform 500ms ease-out;
    transition: transform 500ms ease-out;
    transition: transform 500ms ease-out, -webkit-transform 500ms ease-out;
    width: 30vh;
    height: 40vh;
    margin-left: 5vh;
    margin-bottom: 20px;
    /* margin-bottom: 3vh; */
    /* padding-left: 20vh */
}

.card:hover {
    -webkit-transform: scale(1.02);
            transform: scale(1.02);
}

.card .cards__body > img {
    height: 12rem;
    width: 100%;
    object-fit: cover;
}

.card .cards__body > h2 {
    padding: 1rem;
    color: white;
}

.card .cards__body > p {
    padding: 0 1rem;
}


